import React from "react";
import { connect } from "react-redux";
import Profile from "./Profile";
import {
  thunkGetProfile,
  thunkGetStatus,
  thunkUpdateStatus,
  thunkSavePhoto,
} from "../../Redux/ProfileReducer";
import { withRouter } from "react-router";
import { WithAuthRedirect } from "../../HOC/withAuthRedirect";
import { compose } from "redux";

class ProfileContainer extends React.Component {
  refreshProfile() {
    let userId = this.props.match.params.userId;
    if (!userId) {
      // userId = this.props.authorizedUserId;
      // if (!userId) {
      //   this.props.history.push("/login");
      // }
      userId = 21081;
      //  ХардКод если не найду решение. иначе работает просто прелоадер и не появлется профайлИнфо. UserId приходит как Null и процесс зацикливается
    }
    this.props.thunkGetProfile(userId);
    this.props.thunkGetStatus(userId);
  }
  componentDidMount() {
    this.refreshProfile();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.match.params.userId !== prevProps.match.params.userId) {
      this.refreshProfile();
    }
  }

  render() {
    return (
      <div>
        <Profile
          {...this.props}
          profile={this.props.profile}
          status={this.props.status}
          thunkUpdateStatus={this.props.thunkUpdateStatus}
          isOwner={!this.props.match.params.userId}
          thunkSavePhoto={this.props.thunkSavePhoto}
        />
      </div>
    );
  }
}

let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  authorizedUserId: state.auth.userId,
  isAuth: state.auth.isAuth,
});

export default compose(
  connect(mapStateToProps, {
    thunkGetProfile,
    thunkGetStatus,
    thunkUpdateStatus,
    thunkSavePhoto,
  }),
  withRouter,
  WithAuthRedirect
)(ProfileContainer);
