import { profileAPI, usersAPI } from "../API/api";
import { stopSubmit } from "redux-form";

const ADD_POST = "addPosta";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const DELETE_POST = "DELETE_POST";
const SAVE_PHOTO_SUCCESS = "SAVE_PHOTO_SUCCESS";

let initialState = {
  postData: [
    {
      id: 1,
      message: "Hello, how r u?",
      count: 15,
    },
    {
      id: 2,
      message: "Do you love me?",
      count: 20,
    },
  ],
  newPostText: "IT-Kamasutra",
  profile: null,
  status: "",
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      let stateCopy = { ...state };
      let newPosta = {
        id: stateCopy.postData.length + 1,
        message: action.mypoststextarea,
        count: 0,
      };
      stateCopy.postData = [...state.postData];
      stateCopy.postData.push(newPosta);
      return stateCopy;
    }

    case SET_USER_PROFILE: {
      return { ...state, profile: action.profile };
    }
    case SET_STATUS: {
      return { ...state, status: action.status };
    }
    case DELETE_POST: {
      return {
        ...state,
        postData: state.postData.filter((p) => {
          return p.postId !== action.postId;
        }),
      };
    }
    case SAVE_PHOTO_SUCCESS: {
      return {
        ...state,
        profile: { ...state.profile, photos: action.photos },
      };
    }
    default: {
      return state;
    }
  }
};

export const actionCreatorAddPosta = (mypoststextarea) => ({
  type: ADD_POST,
  mypoststextarea,
});
export const actionCreatorDeletePost = (postId) => ({
  type: DELETE_POST,
  postId,
});
export const savePhotoSuccess = (photos) => ({
  type: SAVE_PHOTO_SUCCESS,
  photos,
});
export const setUserProfile = (profile) => ({
  type: SET_USER_PROFILE,
  profile,
});
export const setStatus = (status) => ({ type: SET_STATUS, status });
export const thunkGetProfile = (userId) => async (dispatch) => {
  let response = await usersAPI.getProfile(userId);
  dispatch(setUserProfile(response.data));
};

export const thunkGetStatus = (userId) => async (dispatch) => {
  let data = await profileAPI.getProfileStatus(userId);
  dispatch(setStatus(data));
};

export const thunkUpdateStatus = (status) => async (dispatch) => {
  try {
    let data = await profileAPI.updateProfileStatus(status);
    if (data.resultCode === 0) {
      dispatch(setStatus(status));
    }
  } catch (error) {
    debugger;
  }
};
export const thunkSavePhoto = (file) => async (dispatch) => {
  let data = await profileAPI.savePhoto(file);
  if (data.resultCode === 0) {
    dispatch(savePhotoSuccess(data.data.photos));
  }
};
export const thunksaveProfile = (profile) => async (dispatch, getState) => {
  const userId = getState().auth.userId;
  const data = await profileAPI.saveProfile(profile);

  if (data.resultCode === 0) {
    dispatch(thunkGetProfile(userId));
  } else {
    dispatch(stopSubmit("edit-profile", { _error: data.messages[0] }));
    return Promise.reject(data.messages[0]);
  }
};

export default profileReducer;
